<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="CANNED_REPLY"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="TICKET_ARTICLE_ID"
            :HeaderText="$t('ManageArticles')" :ListData="SAData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{data}">              
                <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                    <td class="text-center single-action tourguide_a">
                       {{ $options.filters.formatDate(data.row.CREATED_AT) }}
                    </td>                    
                </template>
            </template>
        </tg-list>
        <MovePopUp v-if="IsMoveCopy" :performAction="performAction" :articleIds="articleIds"></MovePopUp>
        <TransferPopUp v-if="IsTransfer" :performAction="performAction" :articleIds="articleIds"></TransferPopUp>
    </section>
</template>
<script>
import Schema from './ManageArticle'
import DataServices from "@/services/DataService";
import MovePopUp from './modelMove.vue'
import TransferPopUp from './modelTransfer.vue'
export default {
    components:{MovePopUp,TransferPopUp},
    data() {
        return {
            isLoading: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: [{
                "COLUMN_NAME": "TICKET_ARTICLE_TITLE",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Article Title",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "TICKET_ARTICLE_CATEGORY_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Category",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "TICKET_ARTICLE_KEYWORDS",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Short Description",
                "DISPLAY_ORDER": 1
            },
            {

                "COLUMN_NAME": "USER_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Assign To",
                "DISPLAY_ORDER": 2

            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created At",
                "DISPLAY_ORDER": 2,
                 settings : {
                    isInSlot : true
                 }
            }
            ],
            SAData: [],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            searchCondition: '',            
            IsMoveCopy:false,
            performAction:'',
            articleIds:null,
            IsTransfer:false,
        }
    },
    created:async function () {
        this.FetchData();
        await Schema.bindleftCommonSearchdropdown(this, "Ticketing", "Status");
        await Schema.bindleftCommonSearchdropdown(this, "Ticketing", "Category");
        await Schema.bindleftCommonSearchdropdown(this, "Ticketing", "Users");
    },
    methods: {
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `SortBy=${vm.SortBy}&SortExp=${vm.SortExp}&PageSize=${vm.PageSize}&PageNumber=${vm.PageNumber}&${vm.searchCondition}`;
            DataServices.GetArticleListing(url).then(response => {
                if (response.data.status == 'Success') {
                    vm.isLoading = false
                    if (response.data.result.length > 0) {                        
                        vm.SAData = response.data.result;
                        vm.TotalRecords = response.data.result[0].TOTAL_RECORDS;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.listheaderbuttons[1].isdisabled=true;
                        vm.listheaderbuttons[2].isdisabled=true;
                        vm.listheaderbuttons[3].isdisabled=true;
                    } else {
                        vm.SAData = [];
                    }
                }
            })
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "callClone":
                    this.ActionOnArticle('Clone');
                    break;
                case "commonDeleteData":
                    this.DeleteMultiple();
                    break;                    
                case 'callMove':
                    this.ActionOnArticle('Move');
                    break;
                case 'callAdd':
                    this.CreateArticle();
                    break;
            }
        },
        ActionOnArticle:function(ths){ 
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.performAction=ths;
                vm.IsMoveCopy=true;
                vm.articleIds=val                
            }
        },
        CreateArticle:function(){
            this.$router.push({ name: 'CreateArticle',params: { id: 0 } });
        },
        EditManageArticle:function(ths){
            this.$router.push({ name: 'CreateArticle',params: { id: ths } });
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Transfer':
                        action.isVisible = true;
                        break;                        
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },       
        commonDeleteData: function (event) {            
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("KnowledgeBase")), function () {                    
                    let url = `ids=${val}&tableName=TALYGEN_ticket_article&primaryKey=TICKET_ARTICLE_ID`; 
                    DataServices.DeleteData(url).then(response => { 
                        if (response.data){
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('KnowledgeBase')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                           vm.ShowAlert(vm.$t('DeletedError', [vm.$t('KnowledgeBase')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.FetchData();
                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchConditionJson != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item, index, event) {            
            switch (callbackfunction) {                
                case 'TransferArticle':
                    this.TransferArticle(item);
                    break;
                case 'EditArticle':
                    this.EditManageArticle(item.TICKET_ARTICLE_ID);
                    break;
                case 'ClickOnView':
                    this.ClickOnView(item.TICKET_ARTICLE_ID);
                    break;
            }
        },
        ClickOnView:function(ths){
            debugger
            this.$router.push({ name: 'ArticleDetails',params: { id: ths } });
        },
        TransferArticle:function(ths){            
            var vm=this;
            vm.performAction='TRANSFAR';
            vm.IsTransfer=true;
            vm.articleIds=ths.AssignedUserId 
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        }  
    }
}
</script>
